body {
  padding: 0;
  margin: 0;
  overflow-y: scroll;
  width: 100vw;
  overflow-x: hidden;
}

header {
  position: sticky;
  z-index: 1300;
  top: 0;
  left: 0;
  width: 100vw;
  height: 64px;
  padding-left: 24px;
  padding-right: 24px;
  align-items: center;
  display: grid;
  grid-template-rows: 64px;
  grid-template-columns: auto 1fr repeat(2,auto);
  grid-template-areas: "logo . login menu-icon";
  background-color: #fff;
}

.m-evomodal {
  display: none;
}

.m-evomodal[aria-expanded=true] {
  display: block;
}

.m-evomodal-flyout__content {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2000;
  width: 100vw;
  height: 100dvh;
  display: grid;
  grid-template-rows: 64px auto;
  background-color: #fff;
}

.m-evomodal-flyout__header {
  display: flex;
  justify-content: flex-end;
  height: 64px;
  padding: 0 24px;
}

.m-evomodal-flyout__body {
  position: relative;
  overflow-x: hidden;
  overflow-y: auto;
  overflow: hidden auto;
  padding: 40px 24px;
}

main {
  margin-top: 0;
  display: block;
  width: 100vw;
  background-color: #b3d4fc;
}
